import React from "react"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import foto1 from "../../images/smoothies-2253423_1920.jpg"


const Ubuntu = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Ayunar y vitamina C eficaces contra cánceres"
                meta="Científicos de Milán han descubierto que una dieta de ayuno y vitamina C podría ser más eficaz para tratar algunos tipos de cáncer"
                description="Científicos de Milán han descubierto que una dieta de ayuno y vitamina C podría ser más eficaz para tratar algunos tipos de cáncer"
                lang="es"
            />
            <h1>Un combo de ayuno más vitamina C es eficaz para los cánceres difíciles de tratar, según un estudio.</h1>
            <hr/>
            <p class="introductionText">Cient&iacute;ficos de la USC y del Instituto del C&aacute;ncer IFOM de Mil&aacute;n han descubierto que
                una dieta de ayuno podr&iacute;a ser m&aacute;s eficaz para tratar algunos tipos de c&aacute;ncer cuando
                se combina con la vitamina C.</p>
            <hr/>
            <p></p>
            <p></p>

            <p>En estudios en ratones, los investigadores encontraron que la combinaci&oacute;n retras&oacute; la
                progresi&oacute;n del tumor en m&uacute;ltiples modelos de rat&oacute;n de c&aacute;ncer colorrectal, en
                algunos ratones caus&oacute; una regresi&oacute;n de la enfermedad. Los resultados se publicaron en la
                revista <a className="link" rel="nofollow" href="https://www.nature.com/articles/s41467-020-16243-3">Nature Communications</a>.</p>
            <p></p>
            <p><b>&quot;Por primera vez, hemos demostrado c&oacute;mo una intervenci&oacute;n completamente no
                t&oacute;xica puede tratar eficazmente un c&aacute;ncer agresivo&quot;</b>, dijo Valter Longo, el autor
                principal del estudio y director del Instituto de Longevidad de la USC en la Escuela de
                Gerontolog&iacute;a Leonard Davis de la USC y profesor de ciencias biol&oacute;gicas en el Colegio de
                Letras, Artes y Ciencias Dornsife de la USC. <b>&quot;Hemos tomado dos tratamientos que son estudiados
                extensamente como intervenciones para retrasar el envejecimiento ( Una dieta de ayuno y <a
                    className="link" href="/vitaminaC">Vitamina C </a>) y
                    los hemos combinado como un poderoso tratamiento para el c&aacute;ncer&quot;.</b></p>
            <p></p>
            <div style={{textAlign:`center`,margin:'50px'}}>
                <img src={foto1} style={{width:`50vh`}} alt="batidos de frutas"></img>
            </div>
            <p>Los investigadores dijeron que aunque el ayuno sigue siendo una opci&oacute;n desafiante para los
                pacientes de c&aacute;ncer, una opci&oacute;n m&aacute;s segura y factible es una dieta baja en
                calor&iacute;as, basada en plantas, que hace que las c&eacute;lulas respondan como si el cuerpo
                estuviera ayunando. Sus hallazgos sugieren que un tratamiento de baja toxicidad de la dieta de ayuno
                m&aacute;s vitamina C tiene el potencial de reemplazar tratamientos m&aacute;s t&oacute;xicos.</p>
            <p></p>
            <p>Los resultados de investigaciones anteriores sobre el potencial de la vitamina C para combatir el
                c&aacute;ncer han sido contradictorios. Sin embargo, estudios recientes est&aacute;n empezando a mostrar
                cierta eficacia, especialmente en combinaci&oacute;n con la quimioterapia. En este nuevo estudio, el
                equipo de investigaci&oacute;n quiso averiguar si una dieta de ayuno que imitara a la dieta normal
                podr&iacute;a mejorar la acci&oacute;n de la vitamina C en altas dosis para combatir los tumores,
                creando un ambiente que ser&iacute;a insostenible para las c&eacute;lulas cancer&iacute;genas, pero que
                a la vez ser&iacute;a seguro para las c&eacute;lulas normales.</p>
            <p></p>
            <p>&quot;Nuestro primer experimento in vitro mostr&oacute; efectos notables&quot;, dijo Longo. <b>&quot;Cuando
                se usa sola, la dieta de ayuno o la vitamina C por s&iacute; sola reduce el crecimiento de las
                c&eacute;lulas cancerosas y causa un peque&ntilde;o aumento en la muerte de las c&eacute;lulas
                cancerosas&quot;. Pero cuando se usaron juntos, tuvieron un efecto dram&aacute;tico, matando casi todas
                las c&eacute;lulas cancerosas&quot;.</b></p>
            <p></p>
            <p>Longo y sus colegas detectaron este fuerte efecto s&oacute;lo en las c&eacute;lulas cancerosas que
                ten&iacute;an una mutaci&oacute;n que es considerada como uno de los objetivos m&aacute;s desafiantes en
                la investigaci&oacute;n del c&aacute;ncer. Estas mutaciones en el gen KRAS se&ntilde;alan que el cuerpo
                se resiste a la mayor&iacute;a de los tratamientos contra el c&aacute;ncer, y reducen la tasa de
                supervivencia del paciente. Las mutaciones del KRAS ocurren en aproximadamente un cuarto de todos los
                c&aacute;nceres humanos y se estima que ocurren en hasta la mitad de todos los c&aacute;nceres
                colorrectales.</p>
            <p></p>
            <p>El estudio tambi&eacute;n proporcion&oacute; pistas acerca de por qu&eacute; los estudios previos de la
                vitamina C como potencial terapia contra el c&aacute;ncer mostraron una eficacia limitada. <b>Por
                s&iacute; mismo, un tratamiento con vitamina C parece desencadenar que las c&eacute;lulas mutadas por el
                KRAS protejan las c&eacute;lulas cancerosas al aumentar los niveles de ferritina, una prote&iacute;na
                que se une al hierro.</b> Pero al reducir los niveles de ferritina, los cient&iacute;ficos lograron aumentar
                la toxicidad de la vitamina C para las c&eacute;lulas cancerosas. En medio de este hallazgo, los
                cient&iacute;ficos tambi&eacute;n descubrieron que los pacientes de c&aacute;ncer colorrectal con altos
                niveles de la prote&iacute;na de uni&oacute;n al hierro tienen menos posibilidades de sobrevivir.</p>
            <p></p>
            <p>&quot;En este estudio, observamos c&oacute;mo los ciclos de dieta que imitan el ayuno son capaces de
                aumentar el efecto de las dosis farmacol&oacute;gicas de vitamina C contra los c&aacute;nceres con
                mutaci&oacute;n del KRAS&quot;, dijo Maira Di Tano, coautora del estudio en el IFOM, Instituto de
                Oncolog&iacute;a Molecular de la FIRC en Mil&aacute;n, Italia. &quot;Esto ocurre a trav&eacute;s de la
                regulaci&oacute;n de los niveles de hierro y de los mecanismos moleculares involucrados en el
                estr&eacute;s oxidativo. Los resultados se&ntilde;alaron en particular un gen que regula los niveles de
                hierro: la hemo-oxigenasa-1&quot;.</p>
            <p></p>
            <p><b>Los estudios previos del equipo de investigaci&oacute;n mostraron que el ayuno y una dieta que imita el
                ayuno retrasa la progresi&oacute;n del c&aacute;ncer y hace que la quimioterapia sea m&aacute;s eficaz
                en las c&eacute;lulas tumorales, a la vez que protege a las c&eacute;lulas normales de los efectos
                secundarios asociados a la quimioterapia. La combinaci&oacute;n mejora la respuesta antitumoral del
                sistema inmunol&oacute;gico en modelos de ratones con c&aacute;ncer de mama y melanoma.</b></p>
            <p></p>
            <p>Los cient&iacute;ficos creen que el c&aacute;ncer se tratar&aacute; eventualmente con medicamentos de
                baja toxicidad de manera similar a como se usan los antibi&oacute;ticos para tratar infecciones que
                matan bacterias particulares, pero que pueden ser sustituidos por otros medicamentos si el primero no es
                efectivo.</p>
            <p></p>
            <p>Para avanzar hacia ese objetivo, dicen que primero deben probar dos hip&oacute;tesis: que sus
                intervenciones combinadas no t&oacute;xicas funcionen en ratones, y que se ver&iacute;a prometedor para
                los ensayos cl&iacute;nicos en humanos. En este nuevo estudio, dijeron que han demostrado ambas. Al
                menos cinco ensayos cl&iacute;nicos, incluyendo uno en la USC en pacientes con c&aacute;ncer de mama y
                c&aacute;ncer de pr&oacute;stata, est&aacute;n investigando ahora los efectos de las dietas que imitan
                el ayuno en combinaci&oacute;n con diferentes drogas para combatir el c&aacute;ncer.</p>
            <hr/>
            <p><b>Fuente: </b><a class="link" rel="nofollow" href="https://www.nature.com/articles/s41467-020-16243-3">https://www.nature.com/articles/s41467-020-16243-3</a></p>
        </Layout>
    )
}

export default Ubuntu
